import React, { useEffect, useState } from "react";
import axios from "axios";
import swal from "sweetalert";
import { API, config } from "../../config";
import FormLoader from "../components/FormLoader/FormLoader";
import FormHeader from "../components/FormHeader/FormHeader";
import FormInput from "../components/FormInput/FormInput";
import FormSubmit from "../components/FormSubmit/FormSubmit";
import classNames from "./Profile.module.css";
import FormUpload from "../components/FormUpload/FormUpload";
import checkLoggedIn from "../../store/global/checkLoggedIn";
import { NavLink } from "react-router-dom";

const Profile = ({ history }) => {
  const [loading, setLoading] = useState(false);
  const [loadingPass, setLoadingPass] = useState(false);

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [img, setImg] = useState("");
  const [image, setImgView] = useState("");

  const [password, setPassword] = useState("");
  const [currentPassword, setCurrentPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const setInitialUser = () => {
    if (!localStorage) return;
    const user = JSON.parse(localStorage.getItem("user"));
    setName(user.name);
    setImgView(user.image);
    setEmail(user.email);
  };

  useEffect(() => {
    checkLoggedIn(history)().then((res) => {
      if (!res) history.push("/");
    });
    setInitialUser();
  }, [history]);

  const setImage = (v) => {
    setImg(v);
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setImgView(reader.result);
      }
    };
    reader.readAsDataURL(v);
  };

  const updateUser = () => {
    setLoading(true);
    const data = new FormData();
    if (name.length > 0) {
      data.append("name", name);
    }
    if (email.length > 0) {
      data.append("email", email);
    }
    if (img.type) {
      data.append("image", img);
    }
    axios
      .patch(`${API}/update-user`, data, config())
      .then((response) => {
        setLoading(false);
        localStorage.setItem("user", JSON.stringify(response.data.data.user));

        document.getElementById("name").value = "";
        document.getElementById("email").value = "";
        document.getElementById("image").value = "";

        setInitialUser();
        swal("Success!", "Profile updated successfully!", "success", {
          button: "Okay",
        });
      })
      .catch((err) => {
        setLoading(false);
        swal("Error!", `${err.response.data.message}`, "error", {
          button: "Okay",
        });
      });
  };

  const updatePassword = () => {
    setLoadingPass(true);
    const data = {
      password,
      confirmPassword,
      currentPassword,
    };
    axios
      .patch(`${API}/update-password`, data, config())
      .then((response) => {
        setLoadingPass(false);
        localStorage.setItem("authToken", response.data.token);

        document.getElementById("currentPassword").value = "";
        document.getElementById("newPassword").value = "";
        document.getElementById("confirmPassword").value = "";

        setInitialUser();
        swal("Success!", "Password updated successfully!", "success", {
          button: "Okay",
        });
      })
      .catch((err) => {
        setLoadingPass(false);
        swal("Error!", `${err.response.data.message}`, "error", {
          button: "Okay",
        });
      });
  };

  return (
    <div className={classNames.container}>
      <div className={classNames.profile_image}>
        <div className={classNames.image}>
          <img src={image} alt="Profile" />
        </div>
        <div className={classNames.name}>
          <h1>{name}</h1>
        </div>
        <div className={classNames.navigations}>
          <NavLink to="/app">Go to app</NavLink>
          <NavLink to="/app">Logout</NavLink>
        </div>
      </div>
      <div className={classNames.profile_details}>
        <div className={classNames.update_profile}>
          <FormHeader title="Update Profile" />
          <FormInput
            i={{
              id: "name",
              type: "text",
              placeholder: `${name}`,
              label: "Enter your new name",
              setFn: setName,
            }}
          />
          <FormInput
            i={{
              id: "email",
              type: "email",
              placeholder: `${email}`,
              label: "Enter your new email",
              setFn: setEmail,
            }}
          />
          <FormUpload
            i={{
              id: "image",
              setFn: setImage,
            }}
          />
          {loading ? (
            <FormLoader />
          ) : (
            <FormSubmit text="Update Profile" clickFn={updateUser} />
          )}
        </div>
        <div className={classNames.update_password}>
          <FormHeader title="Update Password" />
          <FormInput
            i={{
              id: "currentPassword",
              type: "password",
              placeholder: "********",
              label: "Current Password",
              setFn: setCurrentPassword,
            }}
          />
          <FormInput
            i={{
              id: "newPassword",
              type: "password",
              placeholder: "********",
              label: "New Password",
              setFn: setPassword,
            }}
          />
          <FormInput
            i={{
              id: "confirmPassword",
              type: "password",
              placeholder: "********",
              label: "Confirm Password",
              setFn: setConfirmPassword,
            }}
          />
          {loadingPass ? (
            <FormLoader />
          ) : (
            <FormSubmit text="Update Password" clickFn={updatePassword} />
          )}
        </div>
        <div className={classNames.delete_user}>
          <FormHeader title="Danger Zone" />
          <FormSubmit text="Delete account" />
        </div>
      </div>
    </div>
  );
};

export default Profile;
